<template>
	<el-form ref="form" :model="form" style="width: 800px" label-width="100px" size="mini">
		<el-form-item label="网站域名">
			<el-input v-model="form.base_uri"  placeholder="">></el-input>
		</el-form-item>
		<el-form-item label="微信AppId">
			<el-input v-model="form.wx_app_id" placeholder="公众号AppId"></el-input>
		</el-form-item>
		<el-form-item label="微信Secret">
			<el-input v-model="form.wx_secret" placeholder="公众号秘钥"></el-input>
		</el-form-item>
		<el-form-item label="微信MchId">
			<el-input v-model="form.wx_mch_id" placeholder="商户号ID"></el-input>
		</el-form-item>


		<el-form-item>
			<el-button type="primary" @click="onSubmit">立即修改</el-button>
			<el-button>取消</el-button>
		</el-form-item>
	</el-form>


</template>

<script>
	export default {
		data() {
			return {
				form: {
					base_uri: '',
					wx_secret: '',
					wx_app_id: '',
					wx_mch_id: '',
				}
			}
		},
		methods: {
			onSubmit() {
				this.$httpPost("admin/confAdd", this.form).then((response) => {
					if (response.code === 0) {
						this.$message({
							message: response.msg,
							center: true,
							type: "success"
						});
					} else {
						this.$message({
							message: response.msg,
							center: true,
							type: "warning"
						});
					}
				}).catch(() => {
					this.$message({
						message: '请求服务器异常, 请稍后再试',
						center: true,
						type: "error"
					});
				})
			},
		},
		activated() {
			this.$httpGet("admin/sysConf").then((response) => {
				if (response.code === 0) {
					this.form = response.data
				}
			}).catch(() => {
				this.$message({
					message: '请求服务器异常, 请稍后再试',
					center: true,
					type: "error"
				});
			})
		}
	}
</script>

